.icon_Twitter {
width: 50px;
height: 50px;
color: rgb(0, 170, 255);
}

.icon_Whatsapp {
width: 50px;
height: 50px;
color: #8bdc13;
}

.icon_Facebook {
width: 50px;
height: 50px;
color: blue;
}

.input-div {
margin-top: 20px;
display: flex;
width: fit-content;
justify-content: center;
align-items: center;
}

.input-div input {
padding: 5px 5px;
background-color: gray;
color: white;
border: none;
width: fit-content;
text-align: center;
margin-top: 10px;
margin-left: 60px;
}

.input-div button {
padding: 10px 10px;
background-color: red;
color: white;
border: none;
border-radius: 25px;
cursor: pointer;
width: fit-content;
margin-top: 10px;
margin-left: 60px;
}

.input-div button:hover {
background-color: red;
transform: translateY(-2px);
}
